import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'
import {start, Start, rainbow, StyledText, Content, ContentGroup, Parent} from './styles.js'

class IndexPage extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover1: false,
      hover2: false,
      hover3: false,
      hover4: false
    }
  }

  mouseOver = (p) => {
    this.setState({[p]: true})
    console.log(this.state.hover)
  }

  mouseOut = (p) => {
    this.setState({[p]: false})
    console.log(this.state.hover)
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>
              <p>
                HI, I'M <StyledText onMouseOver={() => this.mouseOver('hover1')} onMouseOut={() => this.mouseOut('hover1')}>
                Jaden
                </StyledText>. I'M A <StyledText onMouseOver={() => this.mouseOver('hover2')} onMouseOut={() => this.mouseOut('hover2')}>
                <Link to='/code/'>
                Computer Science
                </Link>
                </StyledText> MAJOR AND <StyledText onMouseOver={() => this.mouseOver('hover3')} onMouseOut={() => this.mouseOut('hover3')}>
                <Link to='/art/'>
                Art
                </Link>
                </StyledText> MINOR AT <StyledText onMouseOver={() => this.mouseOver('hover4')} onMouseOut={() => this.mouseOut('hover4')}>
                <a href='https://pomona.edu' target='_blank'>
                Pomona College
                </a>
                </StyledText>.
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default IndexPage


